@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
* {
  font-family: Montserrat;
  box-sizing: border-box;
  color: #333;
}

body {
  font-size: 16px;
  margin: 0;
}

.container {
  margin-top: 45px;
  display: flex;
  justify-content: center;
}
.card {
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
  border-radius: 4px;
  padding: 1rem;
  position: relative;
  width: 25vw;
}

.cardHeader {
  background: #f7c52a;
  padding: 0.75rem 1.5rem;
  border-radius: 2px;
  text-transform: uppercase;
  transform: skewY(-4deg);
  font-size: 1.1rem;
  font-weight: bold;
  display: inline-block;
  position: absolute;
  top: -1rem;
  left: -0.75rem;
}

.cardBody {
  margin-top: 2rem;
}

.inputGroup {
  margin-bottom: 1rem;
}

label {
  font-size: 0.9rem;
}

input {
  border: none;
  padding: 0.5rem 0.1rem;
  width: 100%;
  border-radius: 2px;
  border-bottom: 1px solid #eee;
  transition: all 0.5s;
}

input:focus {
  border-bottom: 1px solid #ccc;
}

button {
  background: #f7c52a;
  border: none;
  padding: 0.75rem 1.5rem;
  border-radius: 2px;
  width: 100%;
  text-transform: uppercase;
  margin-bottom: 1rem;
}

.form-id {
  color: #4242e0fa;
}
.user-form {
  border: 1px solid;
  padding: 10px;
  background: #8080801c;
  margin-bottom: 10px;
}
a {
  text-decoration: none;
  color: #333;
}

.chatroomPage {
  display: grid;
  align-items: center;
  justify-content: center;
  height: 100vh;
  width: 100vw;
  margin: -8px;
}

.chatroomSection {
  width: 40vw;
  height: 80vh;
  margin: auto;
  border: 1px solid #eee;
  position: relative;
}

.chatroomContent {
  position: absolute;
  top: 2.5rem;
  left: 0;
  right: 0;
  bottom: 3.5rem;
  padding: 0.5rem;
  overflow: auto;
}

.chatroomActions {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 0.5rem;
  display: grid;
  grid-template-columns: 1fr 72px;
  grid-gap: 1rem;
  border-top: 1px solid #eee;
}

.chatroomActions button {
  height: 100%;
}

.message {
  margin-bottom: 0.25rem;
}

.otherMessage {
  color: #0099cc;
  font-weight: bold;
}

.ownMessage {
  color: #00cc00;
  font-weight: bold;
}

.topnav {
  display: flex;
  justify-content: space-between;
  border-bottom: 1px solid;
  background: #f7c52a;
}

.left-nav {
  display: flex;
  width: 50%;
  justify-content: space-evenly;
}

.right-nav {
  display: flex;
  width: 18%;
  justify-content: space-evenly;
  align-items: center;
}

a {
  color: #333;
  padding: 20px 20px;
  font-size: 14px;
}

.active {
  color: #f7c52a;
  background: #333;
}

.auth {
  padding: 0;
}

.auth.active {
  display: none;
}

span {
  font-size: 14px;
  cursor: pointer;
}

.card a {
  color: chocolate;
  font-size: 14px;
}

.department-label {
  font-size: 14.4px;
}

.modal {
  background-color: rgba(0, 0, 0, 0.7);
  position: fixed;
  height: 100%;
  width: 100%;
  z-index: 2;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}

.form {
  width: 50vw;
}

.form .inputGroup {
  display: flex;
  justify-content: space-between;
}

.form input {
  width: 50%;
}
.select {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
}

select {
  padding: 8px;
  min-width: 50%;
}

option {
  padding: 10px 0;
}
textarea {
  height: 100px;
  max-width: 50%;
  min-width: 50%;
}

input:disabled {
  cursor: not-allowed;
}

option:disabled {
  cursor: not-allowed;
}

.error {
  color: red;
  font-style: italic;
}

.approval {
  border: 1px solid;
  padding: 10px;
  background: #8080801c;
  margin-bottom: 10px;
}

.approval-button {
  display: flex;
  justify-content: space-evenly;
}

.approval-button button {
  width: unset;
}

